
[v-cloak] {
  display: none;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  color: rgb(20, 20, 20);
}
#app {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
#app h1 {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 28px;
}
.div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.div .film img {
  width: 100%;
}
.loading {
  background: #333;
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 9999;
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 25px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.film-detay {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 20px;
}
.film-detay input {
  width: 50%;
}
.film-detay input,
button {
  padding: 10px 20px;
  margin-bottom: 10px;
}
.hata-mesaji {
  width: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
}
h1.fix {
  margin-bottom: 5px !important;
}

.film-detay input {
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #9e9e9e;
  border-radius: 5px;
  font-weight: 600;
  font-size: 15px;
}
.film-detay input:hover {
  -webkit-box-shadow: 2px 3px 20px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 3px 20px rgba(0, 0, 0, 0.3);
}
.div .film {
  width: 25%;
  padding-bottom: 15px;
  text-align: center;
  margin-bottom: 15px;
  border: 1px solid #5a5a5a;
  margin-right: 15px;
  border-radius: 0px 0px 5px 5px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.div .film:hover {
  -webkit-box-shadow: 1px 5px 20px rgba(0, 0, 0, 0.6);
          box-shadow: 1px 5px 20px rgba(0, 0, 0, 0.6);
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.div .film p {
  margin-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
}









































